$cell-size: 80px;
$board-width: 338px;

* {
  margin: 0;
  padding: 0;
}

body {
  background: #394a6d;
  color: white;
  font-family: "Montserrat", sans-serif;
}

.app {
  margin-top: 15px;
}

.game-info {
  width: $board-width;
  .game-score {
    width: 100px;
    font-size: 15px;
    font-weight: normal;
    background-color: #252525;
  }
}

.game-data {
  background-color: #293462;
  font-size: 15px;
  color: white;
  font-weight: normal;
}

.game-heading {
  color: #f1d4d4;
  font-weight: bold;
  font-size: 22px;
  width: $board-width;
}

.game-data-won {
  background-color: #260c1a;
  font-size: 15px;
  color: #f05d23;
  font-weight: normal;
}

.game-action {
  width: 70px;
  cursor: pointer;
  background-color: #182952;

  .game-reset {
    font-size: 15px;
    font-weight: normal;
  }
}

.cell {
  background-color: #3c3d47;
  width: $cell-size;
  height: $cell-size;
  margin: 3px;
  border-radius: 6px;
}

.btn-group {
  width: $board-width;

  .key-direction {
    font-size: 14px;
    color: #3c9d9b;
    font-style: italic;
  }

  .btn {
    background-color: #293462;
    color: #52de97;
  }
}

.tile {
  background-color: green;
  border-radius: 5px;
  width: $cell-size;
  height: $cell-size;

  p {
    font-size: 20px;
    line-height: 60px;
    margin: 0;
    font-weight: bold;
  }
}

.color-2 {
  background-color: #4b8e8d;
}

.color-4 {
  background-color: #003f5c;
}

.color-8 {
  background-color: #58508d;
}

.color-16 {
  background-color: #bc5090;
}

.color-32 {
  background-color: #e26241;
}

.color-64 {
  background-color: #3e64ff;
}

.color-128 {
  background-color: #01d28e;
}

.color-256 {
  background-color: #5a3d5c;
}

.color-512 {
  background-color: #c355f5;
}

.color-1024 {
  background-color: #c93838;
}

.color-2048 {
  background-color: #a0c334;
}
